.button {
    max-width: 180px;
    width: 100%;
    padding: 15px 0;
    border-radius: 51px;
    font-size: 11px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 1.1px;
    text-transform: uppercase;
    cursor: pointer;
}

.button.button_transparent {
    border: 1px solid $purple-text;
    background-color: transparent;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    color: $purple-text;
}

.button.button_purple-text {
    background: $purple-to-pink;
    color: #fff;
    &--submit {
        max-width: unset;
    }
}